.App {
    width: 1300px;
    min-height: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
}

.title {
    font-size: 40px;
    font-weight: bold;
    margin: 30px 0;
}

/* 검색도구 박스 */
.filterBox {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 50px;
}

/* 검색도구 박스 안 select */
.filterBox>select{
    width: 150px;
}

/* 검색도구 박스 안 검색창 */
.filterBox>input {
    width: 200px;
}

/* 병원리스트 */
.hospitals {
    min-height: 1000px;
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
}

/* 병원리스트의 병원 */
.hospice {
    width: 325px;
    height: min-content;
    display: flex;
    flex-direction: column;
    padding: 15px 40px 15px 10px;
}

/* 병원리스트의 테두리 박스 */
.border {
    display: flex;
    flex-direction: column;
    border: 1px solid black;
    padding: 5px 10px 5px 10px;
}

/* 병원리스트의 병원 안 내용들 */
.border>* {
    /* height: 30%; */
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin: 5px 0;
}

/* 병원리스트의 병원 안 병원이름 */
.hosName {
    font-size: 20px;
    font-weight: bold;
}

/* 병원 detail */
.medical {
    width: 1100px;
    display: flex;
    justify-content: space-between;
    margin: 30px 0;
}

/* 병원 detail 안 모든 박스 */
.medical>article {
    width: 500px;
    height: 300px;
    display: flex;
    flex-direction: column;
}

/* 병원 지도 박스 */
.medicalMap {
    justify-content: center;
    align-items: center;
    border: 1px solid black;
}

/* 병원 정보 박스 */
.medicalInfo {
    justify-content: space-between;
}

/* 병원 정보 박스 안 내용들 */
.medicalInfo>span {
    white-space: pre-wrap;
}